header nav {
  width: 100%;
  margin: auto;
  background: #302f3f;
  box-shadow: inset 0px -3px 8px rgba(255, 255, 255, 0.07);
  backdrop-filter: blur(36px);
  padding: 53px 20px 21px 20px;
  z-index: 10;
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
}
header nav.extraPadding {
  padding: 63px 20px 21px 20px;
}
@media (min-width: 880px) {
  header nav {
    padding: 32px 20px;
  }
  header nav.extraPadding {
    padding: 52px 20px 12px 20px;
  }
  header nav + .overlay {
    display: none;
  }
}
header nav > div {
  display: flex;
  align-items: center;
  max-width: 1106px;
  margin: auto;
}
header nav .logo {
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
  color: #ffffff;
  width: 80%;
  margin-left: 50px;
}
@media (min-width: 880px) {
  header nav .logo {
    width: 22%;
    margin-left: 0px;
  }
}
header nav .logo img {
  width: 110px;
}
header nav .menu-button {
  display: block;
  position: absolute;
}
@media (min-width: 880px) {
  header nav .menu-button {
    display: none;
  }
}
header nav ul {
  transition: all 0.5s;
  display: none;
  position: absolute;
  top: 105px;
  left: 20px;
  width: 200px;
  text-align: left;
  height: 0;
  overflow-y: hidden;
  background: #ffffff;
  box-shadow: 0px 2px 4px rgba(12, 26, 75, 0.04), 0px 4px 20px -2px rgba(50, 50, 71, 0.08);
  border-radius: 10px;
  z-index: 5;
}
header nav ul.show {
  height: fit-content;
  overflow-y: auto;
  display: block;
}
header nav ul li {
  width: 100%;
  padding: 15px 20px;
}
header nav ul li:not(:last-of-type) {
  margin-right: 0;
}
@media (min-width: 880px) {
  header nav ul li:not(:last-of-type) {
    margin-right: 40px;
  }
}
header nav ul li a {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #5f5f68;
  width: 100%;
}
header nav ul li a img {
  width: 24px;
}
header nav ul li.github {
  display: none;
}
header nav ul li.ml-auto button {
  margin-top: 30px;
}
header nav ul li button,
header nav ul li a.primary {
  background: #477db3;
  border-radius: 8px;
  padding: 9px 20px;
  color: #ffffff;
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  min-width: 175px;
  display: flex;
  align-items: center;
  white-space: nowrap;
  margin-top: 15px;
}
header nav ul li button img,
header nav ul li a.primary img {
  height: 24px;
  width: 24px;
}
@media (min-width: 880px) {
  header nav ul li button,
header nav ul li a.primary {
    margin-top: unset;
    margin-left: 24px;
  }
}
header nav ul + a {
  margin-top: 3px;
}
@media (min-width: 880px) {
  header nav ul {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: initial;
    height: initial;
    overflow-y: unset;
    width: 100%;
    background: transparent;
  }
  header nav ul.show {
    display: flex;
    height: unset;
    padding-top: 0;
    overflow-y: unset;
  }
  header nav ul li {
    padding: 0;
    width: fit-content;
  }
  header nav ul li a {
    color: rgba(255, 255, 255, 0.6);
  }
  header nav ul li a.nuxt-link-active {
    color: #ffffff;
  }
  header nav ul li:last-of-type {
    display: none;
  }
  header nav ul li.github {
    display: block;
  }
  header nav ul li.ml-auto {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  header nav ul li.ml-auto button {
    margin-left: 24px;
    margin-top: unset;
  }
}
header nav a.small {
  display: block;
}
@media (min-width: 880px) {
  header nav a.small {
    display: none;
  }
}
header nav .github-star {
  position: fixed;
  top: 0;
  left: 0;
  background: #477db3;
  width: 100vw;
  height: 40px;
  padding: 7px 11px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  color: #fff;
  z-index: 99;
}
@media (min-width: 880px) {
  header nav .github-star {
    font-size: 14px;
    line-height: 24px;
  }
}
header nav .github-star .github-icon {
  height: 20px;
  width: 20px;
  margin-left: 13px;
  margin-right: 13px;
}
header nav .github-star .github-icon:hover {
  cursor: pointer;
}
header nav .github-star .github-icon img {
  height: 18px;
  width: 18px;
}
header nav .github-star button {
  background: #ffffff;
  border: 1px solid #edeff5;
  box-shadow: 0px 2px 8px rgba(12, 26, 75, 0.08), 0px 3px 8px -1px rgba(50, 50, 71, 0.05);
  border-radius: 4px;
  color: #477db3;
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  height: 24px;
  padding: 10px;
  display: flex;
  align-items: center;
  margin-left: 10px;
}
header nav .github-star button img {
  height: 16px;
  width: 16px;
  margin-right: 5px;
}
header .overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(25px);
  transition: all 0.5s;
  opacity: 0;
  pointer-events: none;
  z-index: 2;
}
header .overlay.show {
  opacity: 1;
  pointer-events: all;
}